@charset "UTF-8";
.btn_area {
  display: flex;
  justify-content: center;
  position: relative;
  margin-top: 25px;
  margin-bottom: 60px;
  overflow: hidden;
}

.btn_area .btn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 60px;
  overflow: hidden;
  border: 1px solid #0082E6;
  border-radius: 0;
  background: #0082E6;
  color: #ffffff;
  font-weight: bold;
  font-size: 16px;
  outline: none;
  transition: 0.3s;
}

.btn_area .btn:hover {
  background: #ffffff;
  color: #0082E6;
}

.btn_area .btn.square {
  justify-content: center;
  width: 320px;
  max-width: 320px;
  height: 50px;
  margin: 0 auto;
  border: 1px solid #000000;
  border-radius: 0;
  background: #ffffff;
  color: #000000;
}

.btn_area .btn.square::after {
  position: absolute;
  right: 1em;
}

.btn_area .btn_orange {
  background-color: #f8833e;
}

.btn_area .btn_black {
  background-color: #000000;
}

.more-btn {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  align-items: center;
  margin: 0 auto;
  border: 1px solid #000000;
  border-radius: 0;
}

@media (max-width: 440px) {
  .more-btn {
    width: 90%;
  }
}

.more-btn .more {
  display: block;
  position: relative;
  overflow: hidden;
  border-radius: 0;
  font-size: 16px;
  line-height: 2.2;
  text-align: center;
  transition: 0.3s;
}

.more-btn .more:hover {
  color: #ffffff !important;
  font-style: italic;
  cursor: pointer;
}

.more-btn .more:hover a {
  color: #ffffff;
}

.more-btn .more:hover::after {
  left: -10%;
  width: 120%;
}

.more-btn .more::after {
  position: absolute;
  bottom: 0;
  left: 50%;
  left: -10%;
  z-index: -1;
  transform: skewX(15deg);
  width: 0;
  height: 3px;
  height: 120%;
  background: #000000;
  content: "";
  transition: 0.3s;
}

.btn-n {
  display: block;
  position: relative;
  width: 100%;
  max-width: 320px;
  margin: 20px auto;
  padding: 15px;
  border: solid 2px #0082E6;
  border-radius: 5px;
  background-color: #0082E6;
  color: #ffffff;
  font-weight: bold;
  font-size: 16px;
  line-height: 1;
  text-align: center;
}

.btn-n span {
  display: block;
  position: absolute;
  top: 13px;
  right: 10px;
  line-height: 1;
}

.btn-n:hover {
  background-color: #eeeeee;
  color: #0082E6;
}

.btn-n::after {
  position: absolute;
  right: 10px;
}

a.bg_pink:hover {
  opacity: 0.6;
  background-color: #f183c8;
}

a.bg_green:hover {
  opacity: 0.6;
  background-color: #00ad00;
}

.mail::before {
  padding-left: 0.8em;
  font-weight: 900;
  font-size: 1.5em;
  font-family: "Font Awesome 5 Free";
  content: "\f0e0";
}

.angle::after {
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  font-weight: 900;
  font-family: "Font Awesome 5 Free";
  content: "\f105";
}

.required::after {
  padding-right: 10px;
  padding-left: 10px;
  border-radius: 5px;
  background-color: #a20000;
  color: #ffffff;
  font-weight: 900;
  font-size: 14px;
  font-family: "Font Awesome 5 Free";
  content: "必須";
}

.tel::before {
  font-weight: 900;
  font-size: 1.5em;
  font-family: "Font Awesome 5 Free";
  content: "\f095";
}

.fax::before {
  font-weight: 900;
  font-size: 1.5em;
  font-family: "Font Awesome 5 Free";
  content: "\f1ac";
}

.pdf::before {
  font-weight: 900;
  font-family: "Font Awesome 5 Free";
  content: "\f1c1";
}

.movie::before {
  font-weight: 900;
  font-family: "Font Awesome 5 Free";
  content: "\f1c8";
}

.info-circle {
  position: relative;
}

.info-circle::before {
  position: absolute;
  top: 5px;
  left: 0;
  font-weight: 900;
  font-family: "Font Awesome 5 Free";
  content: "\f05a";
}

.btn:not([href]):not([tabindex]) {
  color: #ffffff;
  cursor: pointer;
}

a:not([href]):not([tabindex]) {
  cursor: pointer;
}

html {
  scroll-behavior: smooth;
}

