.rotateY {
  transform: rotateY(180deg);
  transform-style: preserve-3d;
  perspective: 470px;
  backface-visibility: hidden;
}

.rotateY.is-action {
  animation-name: rotateYAnime;
  animation-duration: 3s;
  animation-fill-mode: forwards;
}

@keyframes rotateYAnime {
  from {
    transform: rotateY(180deg);
  }
  to {
    transform: rotateY(0deg);
  }
}
